import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, ParamMap } from '@angular/router';

import { Login } from '../login';
import { LoginService } from '../login.service';
import { UserHomeComponent } from '../user-home/user-home.component';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  loginModel = new Login ('Your LoinId', 'Your Password');
  submitted=false;
  errorMsg='';

  constructor(private route: ActivatedRoute, private router: Router,private _loginService: LoginService) { }

  ngOnInit() {
  }

  goToUserHome(){
    this.router.navigate(['user-home'],{relativeTo: this.route});
  }

  onSubmit(){
  	this.submitted=true;
  	this._loginService.loginfunciton(this.loginModel)
  		.subscribe(
  			data => console.log('Success!',data),
  			error => this.errorMsg = error.statusText
  			)
    this.goToUserHome();
  }
}
