import { Component, OnInit } from '@angular/core';
//import { FormGroup, FormControl } from '@angular/forms';
import {FormBuilder, Validators } from '@angular/forms';

import { forbiddenNameValidator } from '../shared/user-name.validator';
import { UpdateProfileService } from '../update-profile.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {

  bloodgroups = ['A+','A-','B+','B-','AB+','AB-','O+','O-','Other'];

  get name(){
  	return this.updateProfileForm.get('name');
  }

  constructor(private fb: FormBuilder, private _updateProfileService: UpdateProfileService) { }

  updateProfileForm = this.fb.group({
  	name: ['Sheenam', [Validators.required, Validators.minLength(3), forbiddenNameValidator(/password/)]],
  	dob: [''],
  	gender: [''],
  	height: [''],
  	weight: [''],
  	bloodgroup: ['']
  });

  /*updateProfileForm = new FormGroup({
  	name: new FormControl('Sheenam'),
  	dob: new FormControl(''),
  	gender: new FormControl(''),
  	height: new FormControl(''),
  	weight: new FormControl(''),
  	bloodgroup: new FormControl('')
  })*/


  ngOnInit() {
  }

  onSubmit(){
  	console.log(this.updateProfileForm.value);
  	this._updateProfileService.update(this.updateProfileForm.value)
  		.subscribe(
  			response => console.log('Success!',response),
  			error => console.log('Error!',error) 
  			);
  }

}
